* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

body {
    font-family: sans-serif;
    background: #F4F6F8;
}

.App {
    text-align: center;
    max-width: 900px;
    margin: 100px auto;
    margin: auto;
}

.container {
	max-width: 900px;
	margin: 30px auto;
	background-color: #fff;
	height: 1000px;
	padding: 20px 0;
}

.container p {
	margin: 20px auto;
	max-width: 600px;
	line-height: 1.5;
	font-size: 18px;
}

.debug {
	margin: 40px 0 50px;
	text-align: center;
}

.debug button {
	font-size: 15px;
	border: solid 1px #ddd;
	margin: 0 10px;
	padding: 10px 20px;
	background: #fff;
	border-radius: 3px;
	min-width: 130px;
}

.carousel {
	position: relative;
	width: 100%;
	height: 600px;
	display: -ms-flexbox;
	display: flex;
	-webkit-perspective-origin: 50% 50%;
	        perspective-origin: 50% 50%;
	-webkit-perspective: 900px;
	        perspective: 900px;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	margin-top: 60px;
	margin-bottom: 60px;
}

.carousel__spinner {
	position: absolute;
	height: 100%;
	width: 50%;
	-webkit-transform: translateZ(-300px) rotateX(0deg);
	        transform: translateZ(-300px) rotateX(0deg);
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	-webkit-transform-origin: 50% 50%;
	    -ms-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
}

.carousel__spinner[data-right] {
	right: 0;
	left: auto;
}

.carousel__spinner[data-right] .slide__content {
	left: -100%;
}

.carousel__controls {
	position: absolute;
	right: -60px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	top: 0;
	bottom: 0;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: auto;
}

.carousel__controls button {
	position: relative;
	border: 0;
	padding: 0;
	background: transparent;
	color: transparent;
	text-indent: 100%;
	font-size: 0;
	overflow: hidden;
	padding: 10px 15px 10px 15px;
	outline: 0;
}

.carousel__controls button:first-child {
	padding-top: 15px;
}

.carousel__controls button:last-child {
	padding-bottom: 15px;
}

.carousel__controls button:hover {
	cursor: pointer;
}

.carousel__controls button:hover::before {
	background-color: rgba(0,0,0,0.4);
}

.carousel__controls button.active::before {
	background-color: rgba(0,0,0,0.4);
}

.carousel__controls button::before {
	margin: auto;
	content: '';
	display: block;
	height: 10px;
	width: 10px;
	border-radius: 50%;
	background-color: rgba(0,0,0,0.2);
}

.slide {
	position: absolute;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	width: 100%;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	-webkit-transform: translateZ(300px);
	        transform: translateZ(300px);
	visibility: hidden;
	will-change: transform;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}

.slide .slide__content:empty {
	background: rgba(0,0,0,0.1);
	text-align: center;
}

.slide .slide__content:empty::before {
	padding: 20px;
	content: 'Uh oh, this slide has no content';
}

.slide[data-next="true"],
.slide[data-prev="true"],
.slide[data-active="true"] {
	visibility: visible;
}

.slide[data-next="true"] {
	z-index: 1;
}

.slide__mask {
	position: relative;
	overflow: hidden;
	width: 100%;
	display: block;
}

.slide__content {
	color: rgba(0,0,0,0.2);
	font-size: 28px;
	width: 200%;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
}

.slide__content > div {
	position: absolute;
	width: 100%;
	height: 100%;
}

.slide[data-next="true"] {
	-webkit-transform-origin: 50% calc(100% - (300px));
	    -ms-transform-origin: 50% calc(100% - (300px));
	        transform-origin: 50% calc(100% - (300px));
	-webkit-transform: rotateX(270deg) translateZ(300px);
	        transform: rotateX(270deg) translateZ(300px);
}

[data-right] .slide[data-next="true"] {
	-webkit-transform-origin: 50% 0;
	    -ms-transform-origin: 50% 0;
	        transform-origin: 50% 0;
	-webkit-transform: rotateX(90deg) translateY(-300px);
	        transform: rotateX(90deg) translateY(-300px);
}

.slide[data-prev="true"] {
	-webkit-transform-origin: 50% 0;
	    -ms-transform-origin: 50% 0;
	        transform-origin: 50% 0;
	-webkit-transform: rotateX(-270deg) translateY(-300px);
	        transform: rotateX(-270deg) translateY(-300px);
}

[data-right] .slide[data-prev="true"] {
	-webkit-transform-origin: 50% 100%;
	    -ms-transform-origin: 50% 100%;
	        transform-origin: 50% 100%;
	-webkit-transform: rotateX(-90deg) translateY(300px);
	        transform: rotateX(-90deg) translateY(300px);
}

.slide__content,
.slide__content > div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-line-pack: stretch;
	    align-content: stretch;
	-ms-flex-wrap: no-wrap;
	    flex-wrap: no-wrap;
}

.slide__content .media,
.slide__content > div .media {
	position: relative;
	-ms-flex: 1 1 50%;
	    flex: 1 1 50%;
	overflow: hidden;
	-ms-flex-positive: 0;
	    flex-grow: 0;
}

.slide__content .media img,
.slide__content > div .media img {
	max-width: 100%;
	-o-object-fit: cover;
	   object-fit: cover;
	height: 100%;
}

.slide__content .text,
.slide__content > div .text {
	display: -ms-flexbox;
	display: flex;
	-ms-flex: 1 1 50%;
	    flex: 1 1 50%;
	background: #27323c;
	-webkit-box-shadow: 0 -1px 0 #333;
	        box-shadow: 0 -1px 0 #333;
	color: #fff;
	padding: 2em;
	-ms-flex-pack: center;
	    justify-content: center;
	font-family: 'Merriweather', serif;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.slide__content .text h3,
.slide__content > div .text h3 {
	font-size: 20px;
	font-weight: 400;
	text-align: center;
}

.slide__content .text p,
.slide__content > div .text p {
	font-size: 15px;
	line-height: 1.7;
	font-weight: 300;
	opacity: 0.75;
	text-align: center;
	margin: 0;
}

.slide__content h1,
.slide__content > div h1 {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	text-align: center;
	font-family: 'Playfair Display', serif;
	margin: auto;
	color: #fff;
	height: 1em;
}

.slide [data-loc="USA"] .text {
	background-color: #2b2533;
}

.slide [data-loc="China"] .text {
	background-color: #19304a;
}

.slide [data-loc="Peru"] .text {
	background-color: #312f2d;
}

.lines i {
	display: inline;
	font-style: normal;
	font-size: 10px;
	line-height: 22px;
	background-color: #ddd;
}

.lines span {
	display: inline;
	font-size: 15px;
	line-height: 22px;
	opacity: 0;
	color: #333;
}


